body {
  background: #0e1021;
  color: #b0b0b0;
  font-family: Open Sans, sans-serif;
  font-size: 16px;
  padding: 10vh 10vw;
  height: 100vh;
  weight: 100vw;
  margin: 0;
  letter-spacing: 0.2px

}

.App {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 100%;

}

//App header and input

.App-header {
  display: flex;
  justify-content: left;
  flex-direction: column;
  width: 60%;
  padding-bottom: 20px;
  position: relative;
  @media (max-width: 768px) {
    width: 80%;
  }
  @media (max-width: 580px) {
    width: 100%;
  }

  .errorMessage {
    color: #da5656;
  }

  .siteDescription {
    margin-top: 10vh;
    text-align: center;
    font-size: 22px;
    font-weight: 500;
    color: rgba(145, 145, 145, 0.75);
  }
}

.inputForm {
  width: 100%;
  //max-width: 500px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 10px;

  input {
    width: 70%;
    height: 32px;
    border: 2px solid white;
    border-radius: 5px;
    margin-right: 1em;
    padding: 2px 20px;
    font-size: 16px;
    transition: border-color 0.2s ease, box-shadow 0.2s ease;
    outline: none;

    @media (max-width: 425px) {
      width: 55%;
      margin-right: 0.6em;
    }
    @media (max-width: 320px) {
      font-size: 14px;
      width: 57%;
    }

    &:hover {
      border-color: #ffcb47;
    }

    &:focus {
      border-color: #ffcb47;
      box-shadow: 0 0 8px rgba(245, 217, 141, 0.75);
    }
  }

  button {
    width: 20%;
    height: 39px;
    background-color: #fdca43;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    color: #0e1021;
    transition: background-color 0.2s ease;

    &:hover {
      background-color: #fcb810;
    }
    @media (max-width: 425px) {
      width: 30%;
    }
    @media (max-width: 320px) {
      font-size: 14px;
      width: 32%;
    }
  }
}

.localStorageButtons {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  margin-bottom: 20px;

  .localStorageButton {
    background: none;
    border: none;
    color: #cecece;
    cursor: pointer;
    padding: 0;
    font-size: 16px;
    margin-right: 12px;

    &:hover {
      color: #ffcb47;
      text-decoration: underline;
    }
  }
}





.autocompleteList {
  display: flex;
  flex-direction: column;
  background-color: white;
  position: absolute;
  top: 36px;
  z-index: 1;
  border-radius: 0 0 5px 5px;
  color: #0e1021;

  $breakpoints: (
          2560px: 73.6%,
          1920px: 74.8%,
          1600px: 75.7%,
          1440px: 76.36%,
          1024px: 77.2%,
          425px: 78.2%,
          320px: 77%
  );

  @each $max-width, $width in $breakpoints {
    @media (max-width: $max-width) {
      width: $width;
    }
  }

  .autocompleteButton {
    background: none;
    border: none;
    color: #0e1021;
    padding: 15px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    p{
      margin: 0;
    }
    .autocompleteCityName{
      font-size: 16px;
      font-weight: 500;
    }
    .autocompleteCountryName{
      color: #5e5e5e;
    }

    &:hover{
      background-color: #ffe08b;
    }

  }

}

.errorMessage {
  width: 100%;
  max-width: 500px;
}


// weather right now

.weatherRightNow {
  width: 60%;
  display: flex;
  justify-content: center;
  flex-direction: row;
  padding: 60px 0 40px 0;
  @media (max-width: 768px) {
    width: 100%;
  }
  @media (max-width: 425px) {
    flex-wrap: wrap;
  }

  > div:not(:last-child) {
    margin-right: 4vw;
  }
}

.weatherInfo {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;

  .cityName {
    font-size: 24px;
    font-weight: bold;
    color: #cecece;
    letter-spacing: 0.7px;
    text-align: center;
    @media (max-width: 320px) {
      font-size: 23px;
    }
  }
}

.weatherRightNowIcon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .weatherRightNowIconImg{
    width: 100px;
    height: 100px;
  }

  p {
    position: relative;
    text-align: center;
    top: -36px;
    max-width: 100px
  }
}



.weatherTemperature {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 54px;
}

.weatherDescription {
  display: flex;
  justify-content: center;
  flex-direction: column;
  font-size: 18px;

  > div:not(:last-child) {
    margin-bottom: 5px;
  }

}

// forecast weather

.forecastList {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 60px 0 60px 0;

  .forecastListItem {
    list-style-type: none;
    margin-bottom: 30px;
    padding: 0;
    margin-right: 30px;
    @media (max-width: 425px) {
      padding: 0;
    }

    .forecastListDateItem {
      font-size: 18px;
      font-weight: 600;
      letter-spacing: 0.7px;
      color: #cecece;
      padding-bottom: 10px;
    }

    ul {
      margin: 0;
      padding: 0;

      li {
        display: flex;
        flex-direction: row;
        align-items: center;
      }
    }

  }
}

.weatherIcon {
  width: 50px;
}
